import classNames from 'classnames/bind'
import styles from './Step.module.scss'

const cx = classNames.bind(styles)

type StepProps = {
    currentStep: 1 | 2
}

const Step = ({currentStep}: StepProps) => {
    const stepList = ['']

    return (
        <ol className={cx('article')}>
            {stepList.map((item, index) => {
                const isCurrentStep = currentStep === index + 1 //   
                const isPrevStep = currentStep > index + 1 //   
                return (
                    <li className={cx('item', {'current-step': isCurrentStep})} key={`step-${index}`}>
                        {isCurrentStep && <span className="blind"> </span>}
                        <span className={cx('number')}>{index + 1}</span>
                        {item}
                        {isPrevStep && <strong className={cx('text')}></strong>}
                    </li>
                )
            })}
        </ol>
    )
}

export default Step
